.badge {
  display: inline-block;
  position: relative;

  &__count,
  &__dot {
    border-radius: 50%;
    background-color: var(--ui-color-primary);
  }

  &__count {
    width: var(--ui-line-height-md);
    height: var(--ui-line-height-md);
    line-height: var(--ui-line-height-md);
    text-align: center;
    color: var(--ui-color-primary-inverted);
  }

  &__dot {
    width: 12px;
    height: 12px;
    border: var(--ui-border-width-medium) solid var(--ui-color-background-primary);
    position: absolute;
    top: -2px;
    right: -4px;
  }
}